.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  opacity: 0;
  visibility: hidden;
  transition: all var(--animation-speed) ease;
  z-index: 150;
}

.backdrop.visible {
  opacity: 1;
  visibility: visible;
}
