.place-order-page__delivery-and-payment {
  margin-top: 1.5rem;
}

p.place-order-page__payment {
  margin-bottom: 1.5rem;
}

.place-order-page__products-and-total-price {
  margin-top: 1.5rem;
  margin-bottom: 3rem;
  padding-bottom: 1.5rem;
}

.place-order-page__delivery,
.place-order-page__total-price {
  display: flex;
  justify-content: space-between;
}

.place-order-page__products-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.place-order-page__product {
  display: grid;
  grid-template-columns: 0.5fr 2fr 1fr 1fr;
  width: 100%;
  margin-bottom: 2rem;
}

.place-order-page__product:nth-last-child(1) {
  margin-bottom: 0;
}

.place-order-page__product-description {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 1rem;
}

.place-order-page__product-name {
  text-transform: uppercase;
}

.place-order-page__size {
  font-family: var(--main-font-family);
  font-weight: var(--main-font-weight);
  font-size: calc(11px + (23 - 11) * ((100vw - 300px) / (1600 - 300)));
  color: #bbb;
}

.place-order-page__quantity,
.place-order-page__product-price {
  display: flex;
  justify-content: center;
  align-items: center;
}

.place-order-page__total-price {
  margin-top: 3rem;
}

.place-order-page__total-price span {
  font-family: var(--main-font-family);
  font-weight: bold;
  font-size: calc(15px + (27 - 15) * ((100vw - 300px) / (1600 - 300)));
}

@media screen and (min-width: 1024px) {
  .place-order-page__title {
    font-size: 30px;
  }
  .place-order-page__name {
    font-size: 24px;
  }
  .place-order-page p,
  .place-order-page span {
    font-size: 20px;
  }

  .place-order-page__product {
    grid-template-columns: 0.3fr 1.5fr 0.8fr 0.8fr;
  }

  .place-order-page__total-price {
    margin-top: 10rem;
  }
  .place-order-page__total-price span {
    font-size: 22px;
  }

  .place-order-page__btn {
    width: 30%;
    margin-left: auto;
    margin-right: auto;
  }
}

.about-page {
  margin: 4rem 2rem 0 2rem;
}

.about-page__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.about-page__item {
  margin-top: 4rem;
}

@media screen and (min-width: 769px) {
  .about-page {
    margin-left: 8rem;
    margin-right: 8rem;
  }

  .about-page__item {
    margin-top: 6rem;
  }

  .about-page__item img {
    width: 80%;
  }
}

.about-page__item:nth-child(1) {
  margin-top: 0;
}

.about-page__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 3rem;
}

.about-page__text h4,
.about-page__text h5 {
  font-family: var(--main-font-family);
  font-weight: var(--main-font-weight);
  font-size: calc(24px + (36 - 24) * ((100vw - 300px) / (1600 - 300)));
  margin-top: 3rem;
}

.about-page__text h5 {
  margin-left: 5rem;
  font-size: calc(19px + (31 - 19) * ((100vw - 300px) / (1600 - 300)));
}

.about-page__text h4:nth-child(1) {
  margin-top: 0;
}

.about-page__list {
  margin-left: 5rem;
  margin-right: 4rem;
  list-style: disc;
}

@media screen and (min-width: 768px) {
  .about-page__text {
    margin-bottom: 6rem;
  }
  .about-page__text h5 {
    margin-top: 6rem;
    margin-left: 9rem;
    font-weight: bold;
  }
  .about-page__list {
    margin-left: 9rem;
    margin-right: 8rem;
  }
  .about-page__list--accessories {
    margin-top: 3rem;
  }
}

.about-page__material-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.about-page__text p,
.about-page__list-item {
  margin-top: 0.6rem;
  font-size: calc(14px + (26 - 14) * ((100vw - 300px) / (1600 - 300)));
  text-align: center;
  line-height: 160%;
  word-spacing: 1px;
}

.about-page__list-item {
  margin-top: 1rem;
}

.about-page__list-item p {
  text-align: left;
}

p.about-page__text--hug {
  margin-top: 1.5rem;
}

@media screen and (min-width: 1500px) {
  .about-page__text h4 {
    font-size: 36px;
  }
  .about-page__text h5 {
    font-size: 28px;
  }

  .about-page__text p,
  .about-page__list-item {
    font-size: 20px;
  }

  .about-page__item img {
    width: 50%;
  }
}

/* PLANT */

.about-page__text .plant__icon-wrapper svg {
  width: 20px;
}

.about-page__text .plant__icon-wrapper .plant__line-left,
.about-page__text .plant__icon-wrapper .plant__line-right {
  width: 20px;
}

.about-page__text .plant__icon-wrapper .plant__line-left {
  margin-right: 0.5rem;
}

.about-page__text .plant__icon-wrapper .plant__line-right {
  margin-left: 0.5rem;
}

@media screen and (min-width: 768px) {
  .about-page__text .plant__icon-wrapper svg {
    width: 25px;
  }
}

.contact-page {
  margin-top: 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contact-page__name {
  width: 100%;
  text-align: center;
  font-family: var(--header-font-family);
  font-weight: var(--header-font-weight);
  font-size: calc(26px + (38 - 26) * ((100vw - 300px) / (1600 - 300)));
}

.contact-page__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 300px;
  margin-top: 3rem;
}

@media screen and (min-width: 768px) {
  .contact-page__list {
    max-width: 500px;
  }
}

.contact-page__list-item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 50%;
  margin-bottom: 3rem;
  color: #000;
}

.contact-page__list-item:nth-last-child(1) {
  margin-bottom: 0;
}

.contact-page__list-item i {
  font-size: calc(22px + (34 - 22) * ((100vw - 300px) / (1600 - 300)));
  color: var(--accent-color);
}

.contact-page__list-item span {
  margin-left: 2rem;
  font-family: var(--main-font-family);
  font-weight: var(--main-font-weight);
  font-size: calc(13px + (25 - 13) * ((100vw - 300px) / (1600 - 300)));
}

.contact-page__text {
  margin-bottom: 3rem;
  font-family: var(--main-font-family);
  font-weight: var(--main-font-weight);
  font-size: calc(13px + (25 - 13) * ((100vw - 300px) / (1600 - 300)));
  text-align: center;
}

@media screen and (min-width: 1500px) {
  .contact-page__name {
    font-size: 32px;
  }
  .contact-page__list-item i {
    font-size: 30px;
  }
  .contact-page__list-item span,
  .contact-page__text {
    font-size: 20px;
  }
}

.not-found {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.not-fount__btn {
  width: 100%;
  margin: 0 auto;
}

